import React from 'react'
import { Cotizacion, Factura, Nota, conCotizacion, conFactura, conNota } from '../Modelos/Factura'
import Cliente, { conCliente } from '../Modelos/Cliente'
import { ProductosContext, ProductosType } from './Producto'

interface State {
    Facturas: Factura[]
    Cotizaciones: Cotizacion[]
    Notas: Nota[]
    Clientes: Cliente[]
}
export interface VentasType extends ProductosType, State {
    actualizar: () => void
    actCliente: () => void
    actCotizacion: () => void
    actNota: () => void
    actFactura: () => void
}
export const VentasContext = React.createContext<VentasType | null>(null)
class VentasProvider extends React.Component<any, State> {
    interval: any = undefined;
    constructor(props: any) {
        super(props)
        this.state = { Facturas: [], Cotizaciones: [], Notas: [], Clientes: [] }
    }
    render() {
        let seccionContext: ProductosType = this.context;
        return <VentasContext.Provider value={{
            ...this.state, ...seccionContext,
            actualizar: this.set.bind(this),
            actCliente: this.actCliente.bind(this),
            actCotizacion: this.actCotizacion.bind(this),
            actNota: this.actNota.bind(this),
            actFactura: this.actFactura.bind(this),
        }}>
            {this.props.children}
        </VentasContext.Provider>
    }
    componentDidMount() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => p === 'FACTURA'))
            conFactura().cargarListaCache((data) => { this.setState({ Facturas: data }) });
        if (seccionContext.data.Permisos.some(p => (p === 'NOTA' || p === 'FACTURA')))
            conNota().cargarListaCache((data) => { this.setState({ Notas: data }) });
        if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA')))
            conCotizacion().cargarListaCache((data) => { this.setState({ Cotizaciones: data }) });
        if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA' || p === 'CLIENTE')))
            conCliente().cargarListaCache((data) => { this.setState({ Clientes: data }) });
        this.interval = setInterval(this.set.bind(this), 30 * 60 * 1000);
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }
    set() {
        this.actCliente();
        this.actCotizacion();
        this.actNota();
        this.actFactura();
    }
    actCliente() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA' || p === 'CLIENTE')))
            conCliente().cargarListaWeb((data) => { this.setState({ Clientes: data }) });
    }
    actCotizacion() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA')))
            conCotizacion().cargarListaWeb((data) => { this.setState({ Cotizaciones: data }) });
    }
    actNota() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => (p === 'NOTA' || p === 'FACTURA')))
            conNota().cargarListaWeb((data) => { this.setState({ Notas: data }) });
    }
    actFactura() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.some(p => p === 'FACTURA'))
            conFactura().cargarListaWeb((data) => { this.setState({ Facturas: data }) });
    }

}
VentasProvider.contextType = ProductosContext;
export default VentasProvider;
export const SeccionConsumer = VentasContext.Consumer;